#flex-form {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
#flex-form .inputs {
  width: 30%;
}
#flex-form label {
  width: 100%;
  font-family: 'Poppins', sans-serif;
  font-size: 18px;
  padding-top: 25px;
}
#flex-form label:last-child {
  width: 100%;
}
#flex-form input {
  border-color: #004b72;
  border-width: 1.4px;
  margin-top: 10px;
  height: 35px;
  font-family: 'Poppins', sans-serif;
  font-size: 18px;
  width: 100%;
}
#flex-form input:last-child {
  width: 100%;
}
#flex-form input,
#flex-form label {
  display: block;
}
@media (max-width: 800px) {
  #flex-form label {
    width: 100%;
  }
  #flex-form .inputs {
    width: 100%;
  }
  #flex-form .error {
    width: 100%;
  }
}
