#rating-section {
  position: relative;
  background-color: #004b72;
}
#rating-section h1 {
  text-align: center;
  color: #fff;
  padding: 30px;
}
#rating-section .chart-container {
  padding-top: 50px;
  padding-bottom: 200px;
  margin: auto;
  position: relative;
}
@media (min-width: 1680px) {
  #rating-section .chart-container {
    width: 65vw;
  }
}
@media (max-width: 700px) {
  #rating-section .chart-container {
    width: 85vw;
  }
}
