footer {
  background-color: #004b72;
  width: 100%;
  margin-top: 50px;
}
footer ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
footer #copyright {
  height: 45px;
  background-color: #00354C;
  color: #fff;
  text-align: right;
  padding-right: 30px;
  padding-top: 25px;
}
footer #service {
  width: 85%;
  margin: 0 auto;
  display: flex;
  padding: 30px;
  max-width: 1600px;
}
footer #service #flex-service {
  width: 50%;
}
footer #service #flex-service h1 {
  color: #fff;
  font-family: 'Poppins', sans-serif;
  font-size: 18px;
  font-style: italic;
  margin: 0;
}
footer #service #flex-service h2 {
  color: #fff;
  font-size: 24px;
  font-family: 'Noto Serif', sans-serif;
}
footer #service #flex-service #social-media a > img {
  width: 50px;
  height: auto;
  padding-right: 20px;
}
footer #service #flex-service nav {
  margin-top: 150px;
}
footer #service #flex-service nav.underlined:after {
  display: block;
  border-bottom: 0;
  border-top: 1px solid #fff;
}
footer #service #flex-service nav li {
  display: inline-block;
  padding-right: 10px;
}
footer #service #tweets {
  width: 50%;
  color: #fff;
}
footer #service #flex-service,
footer #service #tweets {
  margin-top: 45px;
  padding-right: 50px;
}
@media (max-width: 805px) {
  footer #service {
    flex-direction: column;
  }
  footer #service h1 {
    margin-top: 50px;
  }
  footer #service #flex-service {
    height: 100%;
  }
  footer #service #flex-service,
  footer #service #tweets {
    width: 100%;
  }
  footer #service #tweets {
    disply: flex;
    order: -1;
    margin-top: 20px;
    margin-left: 0;
  }
}
