#flex-thumbs {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
#flex-thumbs input {
  opacity: 0;
}
#flex-thumbs input,
#flex-thumbs label {
  display: block;
  cursor: pointer;
}
#flex-thumbs label {
  text-indent: -9000px;
  width: 40%;
  margin: 30px auto 0;
  opacity: 0.3;
}
#flex-thumbs label.selected,
#flex-thumbs label:hover {
  opacity: 1.0;
}
#flex-thumbs .container {
  position: relative;
  width: 33%;
  box-sizing: border-box;
}
#flex-thumbs .container #insufficient-label {
  background-image: url("/assets/bad.svg");
  background-repeat: no-repeat;
  background-size: cover;
}
#flex-thumbs .container #satisfying-label {
  background-image: url("/assets/average.svg");
  background-repeat: no-repeat;
  background-size: cover;
}
#flex-thumbs .container #satisfactory-label {
  background-image: url("/assets/good.svg");
  background-repeat: no-repeat;
  background-size: cover;
}
#flex-thumbs .container .description {
  display: none;
}
#flex-thumbs .container:hover .description {
  display: block;
}
#flex-thumbs .container .title {
  margin-top: 0;
  margin-bottom: 5px;
}
#flex-thumbs .container .text {
  margin-top: 5px;
}
@media (max-width: 950px) {
  #flex-thumbs .container {
    border-right: none;
  }
  #flex-thumbs .container:not(.satisfactory) {
    border-right: 2px solid #004b72;
  }
  #flex-thumbs .container .description {
    background-color: #fff;
    color: #004b72;
    display: block;
    position: relative;
    margin: 0;
  }
  #flex-thumbs .container .description:after {
    content: none;
  }
}
@media (max-width: 650px) {
  #flex-thumbs .container {
    border: none;
    width: 100%;
  }
  #flex-thumbs .container:not(.satisfactory) {
    border: none;
  }
}
