.error {
  box-sizing: content-box;
  position: absolute;
  background: #D80B55;
  padding: 10px 0px 10px 0px;
  color: #fff;
  width: 30%;
  text-align: center;
  border-radius: 5px;
  z-index: 999;
  margin-top: 15px;
  padding: 10px 5px 10px 0px;
}
.error:before {
  bottom: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-bottom-color: #D80B55;
  border-width: 13px;
}
#rating-error {
  width: 40%;
  padding: 25px;
}
#rating-error:before {
  left: 50px;
}
#rating-error:after {
  position: absolute;
  content: 'x';
  color: #fff;
  right: 20px;
  top: 0px;
}
