body {
  background-color: #0093BC;
  font-size: 18px;
  font-family: 'Open Sans:300', sans-serif;
}
h1 {
  font-weight: normal;
}
h2 {
  font-style: italic;
  font-weight: normal;
  font-family: 'Noto Serif', sans-serif;
}
a {
  color: #49b2d7;
  text-decoration: none;
}
a:target {
  color: #fff;
}
.underlined:after {
  content: "";
  display: block;
  width: 50px;
  border-bottom: 1px solid #fff;
  margin-top: 15px;
}
.active {
  color: #fff;
}
.wrapper {
  width: 85%;
  margin: 0 auto;
  max-width: 1600px;
}
::-webkit-input-placeholder {
  color: #004b72;
}
:-moz-placeholder {
  color: #004b72;
  opacity: 1;
}
::-moz-placeholder {
  color: #004b72;
  opacity: 1;
}
:-ms-input-placeholder {
  color: #004b72;
}
::-ms-input-placeholder {
  color: #004b72;
}
