header {
  font-family: 'Poppins', sans-serif;
  font-size: 18px;
  padding-top: 25px;
  padding-bottom: 15px;
}
header ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
header nav > ul > li {
  display: inline-block;
}
header nav > ul > li:last-child {
  margin-left: 20px;
}
header img {
  display: block;
  width: 200px;
  margin: 0 auto;
  padding-bottom: 50px;
}
