main {
  background-color: #fff;
  box-sizing: border-box;
  padding: 30px;
}
main h2 {
  font-size: 30px;
  font-family: 'Noto Serif', sans-serif;
  color: #000;
}
main h1,
main h2 {
  text-align: center;
}
main h1 {
  font-size: 18px;
  color: #004b72;
  font-family: 'Poppins', sans-serif;
}
main h1.underlined:after {
  border-color: #004b72;
  margin: 0 auto;
  padding-top: 10px;
}
main #satisfaction {
  margin-top: 30px;
  color: #004b72;
  font-family: 'Poppins', sans-serif;
  font-size: 18px;
}
main #rating {
  position: relative;
}
