#info {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 50px;
  min-height: 450px;
}
#info #about-me {
  width: 75%;
  background-color: #fff;
}
#info #about-me .indent {
  width: 85%;
  margin: 0 auto;
}
#info #about-me .indent h1 {
  font-family: 'Poppins', sans-serif;
  font-size: 18px;
  color: #004b72;
}
#info #about-me .indent h1.underlined:after {
  border-color: #004b72;
}
#info #about-me .indent h2 {
  font-family: 'Noto Serif', sans-serif;
  font-style: italic;
  font-size: 24px;
}
#info #statistics {
  width: 20%;
  background-color: #004b72;
  display: flex;
  align-items: center;
  justify-content: center;
}
#info #statistics img {
  display: block;
  margin: 0 auto;
}
#info #statistics figcaption {
  text-align: center;
  margin-top: 10px;
  font-family: 'Noto Serif', sans-serif;
  font-size: 20px;
  font-style: italic;
  font-weight: normal;
  color: #fff;
}
@media (max-width: 950px) {
  #info #about-me,
  #info #statistics {
    width: 100%;
  }
  #info #statistics {
    margin-top: 20px;
  }
  #info #statistics img {
    width: 80%;
    margin-top: 10px;
  }
}
