main form > input[type="submit"] {
  width: 350px;
  height: 50px;
  display: block;
  margin: 0 auto;
  background-color: #004b72;
  border: none;
  color: #fff;
  margin-top: 50px;
  font-family: 'Noto Serif', sans-serif;
  font-size: 24px;
  font-style: italic;
}
@media (max-width: 950px) {
  main form > input[type="submit"] {
    width: 300px;
    height: 40px;
  }
}
